import path from "path";
export function printWithColor(variableName, variableValue, depth = null, colors = true) {
    // 
    const error = new Error();
    const stack = error.stack;
    let file = "unknown file";
    let line = "unknown line";
    if (stack) {
        const callerLine = stack.split("\n")[2]; // 
        const regex = /\((.*?):(\d+):\d+\)/; // 
        const match = regex.exec(callerLine);
        if (match) {
            // file = match[1]; ///home/runner/work/github-actions-cicd/github-actions-cicd/.github/actions/aws-genai-codereview/src/review.ts
            file = path.basename(match[1]); //  path.basename review.ts
            line = match[2];
        }
    }
    // 
    //  console.dir console.log
    // depth: null
    // colors: true
    console.log(`\n\n\x1b[36m%s\x1b[0m`, `Printing ${variableName} <${file}:${line}>`);
    console.dir(variableValue, { depth, colors });
}
